import React, { useState } from 'react'
import { connect } from 'react-redux'

import { TransformWrapper } from 'react-zoom-pan-pinch'
import MindMap from './MindMap'
import * as actions from '../../store/actions'
import { throttle } from 'throttle-debounce'

const MindMapWrap = props => {
    const [eventIsFromDraggable, setEventIsFromDraggable] = useState(false)
    const throttleFunc = throttle(10, () => props.addDragStep())
    return (
        <TransformWrapper
            defaultScale={0.5}
            defaultPositionX={200}
            defaultPositionY={100}
            options={{
                limitToBounds: false,
                minScale: 0.1,
                maxScale: 3,
                disabled: eventIsFromDraggable,
            }}
            pan={{
                velocity: false,
            }}
            wheel={{
                step: 300,
            }}
            onZoomChange={value => {
                props.setScaleMM(value.scale)
                throttleFunc()
            }}
            onPanning={throttleFunc}
            onPanningStop={throttleFunc}
            onPinching={throttleFunc}
            onPinchingStop={throttleFunc}
            doubleClick={{
                disabled: true,
            }}
            zoomIn={{
                step: 1,
                animation: false,
            }}
            zoomOut={{
                step: 1,
                animation: false,
            }}
        >
            {({ setScale, resetTransform }) => (
                <MindMap
                    setEventIsFromDraggable={setEventIsFromDraggable}
                    setScale={setScale}
                    resetTransform={resetTransform}
                />
            )}
        </TransformWrapper>
    )
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
        setScaleMM: value => dispatch(actions.setScaleMM(value)),
    }
}

export default connect(
    null,
    mapDispatchToProps
)(MindMapWrap)
