import React, { useState } from 'react'
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Divider,
    IconButton,
    Menu,
    MenuItem,
    Popover,
    Slide,
    Slider,
    TextField,
} from '@material-ui/core'
import {
    Add,
    Brush,
    Check,
    CheckCircle,
    Delete,
    Edit,
    Image,
    OpenInNew,
    Send,
    Title,
    Videocam,
} from '@material-ui/icons'
import { useTranslation } from 'react-i18next'

import Goal from '../Goal/Goal'
import Reward from '../Reward/Reward'
import Activity from '../Activity/Activity'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import EditResource from '../../components/EditResource/EditResource'
import Profile from '../Profile/Profile'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import FolderMM from './FolderMM'
import ProfileMM from './ProfileMM'
import RewardMM from './old/RewardMM'
import GoalMM from './old/GoalMM'
import ActivityMM from './old/ActivityMM'
import { saveValue } from './actions'
import { SketchPicker } from 'react-color'

const TransitionComponent = props => <Slide direction="up" {...props} />

const ChooseWrapMM = props => {
    const { t } = useTranslation()
    const [startResourceVisible, setStartResourceVisible] = useState('')

    const [videoURLOpen, setVideoURLOpen] = useState(false)
    const [anchorElRadius, setAnchorElRadius] = useState(null)
    const [anchorElbgColor, setAnchorElbgColor] = useState(null)
    const [anchorElLineColor, setAnchorElLineColor] = useState(null)
    const [anchorElLineWidth, setAnchorElLineWidth] = useState(null)
    const [anchorEl, setAnchorEl] = useState(null)
    const [anchorElLine, setAnchorElLine] = useState(null)
    const [editingStyle, setEditingStyle] = useState(false)
    const [editingText, setEditingText] = useState(false)
    const [editingTextModal, setEditingTextModal] = useState(false)
    const [textValue, setTextValue] = useState()
    const {
        el,
        id,
        selectedBox,
        setChangeParentMode,
        changeParentMode,
        addEmptyToStructure,
        editorMode,
        account,
        addBlock,
        saveValueMM,
        deleteBlock,
    } = props
    const [videoURL, setVideoURL] = useState(el.videoURL)
    const [openElement, setOpenElement] = useState(
        props.history.location.hash.length > 0 &&
            (id === props.history.location.hash.substring(1) ||
                (el.type === 'profile' &&
                    props.history.location.hash.substring(1)) === 'profile')
    )

    const addMenu = (
        <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={() => setAnchorEl(null)}
        >
            <MenuItem
                onClick={() => {
                    addBlock(id, 'text')
                    setAnchorEl(null)
                }}
            >
                {t('g.text')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    addBlock(id, 'image')
                    setAnchorEl(null)
                }}
            >
                {t('g.image')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    addBlock(id, 'block')
                    setAnchorEl(null)
                }}
            >
                {t('g.block')}
            </MenuItem>
            <MenuItem
                onClick={() => {
                    setStartResourceVisible('goal')
                    setAnchorEl(null)
                }}
            >
                {t('g.goal')}
            </MenuItem>
        </Menu>
    )
    const lineTypeMenu = (
        <Menu
            anchorEl={anchorElLine}
            open={Boolean(anchorElLine)}
            onClose={() => setAnchorElLine(null)}
        >
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'fluid' })
                    setAnchorElLine(null)
                }}
            >
                Fluid
            </MenuItem>
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'straight' })
                    setAnchorElLine(null)
                }}
            >
                Straight
            </MenuItem>
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'arc' })
                    setAnchorElLine(null)
                }}
            >
                Arc
            </MenuItem>
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'magnet' })
                    setAnchorElLine(null)
                }}
            >
                Magnet
            </MenuItem>
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'grid' })
                    setAnchorElLine(null)
                }}
            >
                Grid
            </MenuItem>
            <MenuItem
                onClick={() => {
                    saveValueMM(id, { lineType: 'no' })
                    setAnchorElLine(null)
                }}
            >
                No line
            </MenuItem>
        </Menu>
    )
    const lineColorMenu = (
        <Popover
            id={id}
            open={Boolean(anchorElLineColor)}
            anchorEl={anchorElLineColor}
            onClose={() => setAnchorElLineColor(null)}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            className="noPan"
        >
            <div onMouseDown={e => e.stopPropagation()}>
                <SketchPicker
                    color={el.lineColor}
                    onChange={color =>
                        saveValueMM(id, {
                            lineColor: color.hex,
                        })
                    }
                    onChangeComplete={color =>
                        saveValueMM(id, {
                            lineColor: color.hex,
                        })
                    }
                />
            </div>
        </Popover>
    )
    const lineWidthMenu = (
        <Popover
            id={id}
            open={Boolean(anchorElLineWidth)}
            anchorEl={anchorElLineWidth}
            onClose={() => setAnchorElLineWidth(null)}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            className="noPan"
        >
            <div
                onMouseDown={e => e.stopPropagation()}
                style={{ padding: '8px 15px' }}
            >
                <Slider
                    min={1}
                    max={20}
                    step={1}
                    value={el.lineWidth}
                    onChange={(e, value) =>
                        saveValueMM(id, { lineWidth: value })
                    }
                    style={{
                        width: '200px',
                    }}
                />
            </div>
        </Popover>
    )

    const bgColorMenu = (
        <Popover
            id={id}
            open={Boolean(anchorElbgColor)}
            anchorEl={anchorElbgColor}
            onClose={() => setAnchorElbgColor(null)}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            className="noPan"
        >
            <div onMouseDown={e => e.stopPropagation()}>
                <SketchPicker
                    color={el.bgColor}
                    onChange={color =>
                        saveValueMM(id, {
                            bgColor: color.hex,
                        })
                    }
                    onChangeComplete={color =>
                        saveValueMM(id, {
                            bgColor: color.hex,
                        })
                    }
                />
            </div>
        </Popover>
    )
    const radiusMenu = (
        <Popover
            id={id}
            open={Boolean(anchorElRadius)}
            anchorEl={anchorElRadius}
            onClose={() => setAnchorElRadius(null)}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            className="noPan"
        >
            <div
                onMouseDown={e => e.stopPropagation()}
                style={{ padding: '8px 15px' }}
            >
                <Slider
                    min={0}
                    max={50}
                    step={1}
                    value={el.radius}
                    onChange={(e, value) => saveValueMM(id, { radius: value })}
                    style={{
                        width: '200px',
                    }}
                />
            </div>
        </Popover>
    )

    const videoDialog = (
        <Dialog
            open={videoURLOpen}
            onClose={() => setVideoURLOpen(false)}
            fullWidth
            onMouseDown={e => e.stopPropagation()}
        >
            <DialogTitle>{t('g.addMedia')}</DialogTitle>
            <DialogContent>
                <DialogContentText>{t('g.addMediaDesc')}</DialogContentText>
                <TextField
                    autoFocus
                    margin="dense"
                    label="URL"
                    type="url"
                    fullWidth
                    defaultValue={el.videoURL}
                    onChange={e => setVideoURL(e.target.value)}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setVideoURLOpen(false)} color="primary">
                    {t('g.cancel')}
                </Button>
                <Button
                    onClick={() => {
                        saveValueMM(id, { videoURL })
                        setVideoURLOpen(false)
                    }}
                    color="primary"
                >
                    {t('g.confirm')}
                </Button>
            </DialogActions>
        </Dialog>
    )

    const handleSaveText = () => {
        saveValueMM(id, { textValue })
        setEditingText(false)
        setEditingTextModal(false)
    }

    return (
        <>
            <div
                style={{
                    flex: 1,
                    display: 'flex',
                    overflow: 'hidden',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        padding: '5px',
                        textAlign: 'right',
                        position: 'absolute',
                        top: '-50px',
                        whiteSpace: 'nowrap',
                        alignItems: 'start',
                    }}
                    className="buttonsWrapMM buttonsMM"
                >
                    {el.type !== 'folder' &&
                        (!editorMode || selectedBox === id) &&
                        !editingText &&
                        !editingStyle && (
                            <Button
                                onClick={() => setOpenElement(true)}
                                color="primary"
                                variant="contained"
                                style={{ marginRight: '10px' }}
                            >
                                {t('g.open')}
                            </Button>
                        )}
                    {editorMode && selectedBox === id && (
                        <>
                            {editingStyle ? (
                                <div>
                                    <Button
                                        onClick={() => setEditingStyle(false)}
                                        style={{ marginRight: '10px' }}
                                        color="primary"
                                    >
                                        {t('g.back')}
                                    </Button>
                                    <Button
                                        onClick={e => {
                                            setAnchorElLine(e.currentTarget)
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineType')}
                                    </Button>
                                    <Button
                                        onClick={e => {
                                            setAnchorElLineColor(
                                                e.currentTarget
                                            )
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineColor')}
                                    </Button>
                                    <Button
                                        onClick={e => {
                                            setAnchorElLineWidth(
                                                e.currentTarget
                                            )
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.lineWidth')}
                                    </Button>
                                    <Button
                                        onClick={e => {
                                            setAnchorElbgColor(e.currentTarget)
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.background')}
                                    </Button>
                                    <Button
                                        onClick={e => {
                                            setAnchorElRadius(e.currentTarget)
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        {t('g.corners')}
                                    </Button>
                                    {lineColorMenu}
                                    {lineTypeMenu}
                                    {lineWidthMenu}
                                    {radiusMenu}
                                    {bgColorMenu}
                                </div>
                            ) : editingText ? (
                                <div
                                    style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        position: 'relative',
                                        top: '-39px',
                                        background: 'white',
                                        alignItems: 'start',
                                    }}
                                >
                                    <Button
                                        onClick={handleSaveText}
                                        color="primary"
                                        style={{
                                            marginBottom: '10px',
                                        }}
                                    >
                                        {t('g.back')}
                                    </Button>
                                    <Button
                                        onClick={() => {
                                            setEditingTextModal(true)
                                        }}
                                        color="primary"
                                    >
                                        {t('g.fullscreen')}
                                    </Button>
                                </div>
                            ) : (
                                <>
                                    <Button
                                        onClick={e => {
                                            setAnchorEl(e.currentTarget)
                                        }}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        <Add />
                                    </Button>
                                    {addMenu}
                                    {el.type === 'folder' && (
                                        <>
                                            <Upload
                                                accept="image/*"
                                                showUploadList={false}
                                                customRequest={({ file }) => {
                                                    uploadFile(
                                                        file,
                                                        800,
                                                        undefined,
                                                        url => {
                                                            if (url) {
                                                                saveValueMM(
                                                                    id,
                                                                    {
                                                                        imageValue: url,
                                                                    }
                                                                )
                                                            }
                                                        }
                                                    )
                                                }}
                                            >
                                                <Button
                                                    variant="contained"
                                                    style={{
                                                        marginRight: '10px',
                                                    }}
                                                >
                                                    <Image />
                                                </Button>
                                            </Upload>
                                            <Button
                                                onClick={() =>
                                                    setVideoURLOpen(true)
                                                }
                                                variant="contained"
                                                style={{
                                                    marginRight: '10px',
                                                }}
                                            >
                                                <Videocam />
                                            </Button>
                                            {videoDialog}
                                            <Button
                                                onClick={() =>
                                                    setEditingText(true)
                                                }
                                                variant="contained"
                                                style={{
                                                    marginRight: '10px',
                                                }}
                                            >
                                                <Title />
                                            </Button>
                                        </>
                                    )}
                                    <Button
                                        onClick={() => setEditingStyle(true)}
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        <Brush />
                                    </Button>
                                    <Button
                                        onClick={() =>
                                            setChangeParentMode(
                                                changeParentMode === id
                                                    ? ''
                                                    : id
                                            )
                                        }
                                        color={
                                            changeParentMode
                                                ? 'primary'
                                                : 'default'
                                        }
                                        variant="contained"
                                        style={{ marginRight: '10px' }}
                                    >
                                        <Send />
                                    </Button>
                                    {el.type === 'folder' && (
                                        <Button
                                            onClick={() => deleteBlock(id)}
                                            variant="contained"
                                            style={{ marginRight: '10px' }}
                                        >
                                            <Delete />
                                        </Button>
                                    )}
                                </>
                            )}
                        </>
                    )}
                </div>
                <div style={{ flex: 1, overflow: 'hidden', display: 'flex' }}>
                    {el.type === 'folder' && (
                        <FolderMM
                            el={el}
                            id={id}
                            editingText={editingText}
                            setEditingText={setEditingText}
                            editingTextModal={editingTextModal}
                            setEditingTextModal={setEditingTextModal}
                            selectedBox={selectedBox}
                            saveValueMM={saveValueMM}
                            textValue={textValue}
                            setTextValue={setTextValue}
                            handleSaveText={handleSaveText}
                            editorMode={editorMode}
                        />
                    )}
                    {el.type === 'profile' && <ProfileMM />}

                    {/* {el.type === 'reward' && <RewardMM id={id} />} */}
                    {el.type === 'goal' && <GoalMM id={id} />}
                    {/* {el.type === 'activity' && <ActivityMM id={id} />} */}
                </div>
            </div>
            <DialogFullscreen
                open={openElement}
                close={() => setOpenElement(false)}
                title="Opened res"
            >
                {el.type === 'profile' ? (
                    <Profile setChosenResource={setOpenElement} />
                ) : el.type === 'reward' ? (
                    <Reward id={el.id} setChosenResource={setOpenElement} />
                ) : el.type === 'activity' ? (
                    <Activity id={el.id} setChosenResource={setOpenElement} />
                ) : el.type === 'goal' ? (
                    <Goal id={el.id} setChosenResource={setOpenElement} />
                ) : null}
            </DialogFullscreen>
            {account._id && editorMode && (
                <Dialog
                    fullScreen
                    open={Boolean(startResourceVisible)}
                    onClose={() => {
                        setStartResourceVisible('')
                    }}
                    TransitionComponent={TransitionComponent}
                    scroll="paper"
                >
                    <EditResource
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setStartResourceVisible('')}
                        type={startResourceVisible}
                        addEmptyToStructure={addEmptyToStructure}
                        selectedBox={id}
                    />
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
        addEmptyToStructure: (id, selectedBox, type) =>
            dispatch(actions.addEmptyToStructure(id, selectedBox, type)),
        addBlock: (id, blockType) => dispatch(actions.addBlock(id, blockType)),
        saveValueMM: (id, value) => dispatch(actions.saveValueMM(id, value)),
        deleteBlock: id => dispatch(actions.deleteBlock(id)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ChooseWrapMM)
)
