import React, { useState } from 'react'
import ReactPlayer from 'react-player/lazy'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import TinyEditor from '../../components/UI/TinyEditor'

const FolderMM = props => {
    const {
        selectedBox,
        el,
        id,
        editingText,
        setEditingText,
        saveValueMM,
        editingTextModal,
        setEditingTextModal,
        textValue,
        setTextValue,
        handleSaveText,
        editorMode,
    } = props

    return (
        <div
            style={{
                background: el.bgColor || 'white',
                flex: 1,
                borderRadius: (el.radius || 0) + '%',
            }}
        >
            {el.imageValue && (
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        position: 'absolute',
                        left: 0,
                        right: 0,
                        top: 0,
                        bottom: 0,
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: 'center',
                        backgroundImage: 'url("' + el.imageValue + '")',
                    }}
                />
            )}

            {el.videoURL && (
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        position: 'absolute',
                        left: 0,
                        right: 0,
                        top: 0,
                        bottom: 0,
                    }}
                >
                    <ReactPlayer
                        url={el.videoURL}
                        width="100%"
                        height="100%"
                        style={{
                            overflow: 'hidden',
                        }}
                    />
                </div>
            )}
            {editorMode && (
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        position: 'absolute',
                        left: 0,
                        right: 0,
                        top: 0,
                        bottom: 0,
                    }}
                />
            )}

            {(el.textValue || editingText) && (
                <div
                    className={
                        editingText && selectedBox === id
                            ? 'tinyEditorText_'
                            : ''
                    }
                >
                    <TinyEditor
                        onEditorChange={value => setTextValue(value)}
                        MM
                        value={el.textValue}
                        readOnly={!(editingText && selectedBox === id)}
                    />
                </div>
            )}
            <div className="tinyEditorText_">
                <DialogFullscreen
                    open={editingText && selectedBox === id && editingTextModal}
                    close={() => {
                        setEditingText(false)
                    }}
                    save={handleSaveText}
                >
                    <div className="tinyEditorText_">
                        {editingText && selectedBox === id && (
                            <TinyEditor
                                onEditorChange={value => setTextValue(value)}
                                value={el.textValue}
                            />
                        )}
                    </div>
                </DialogFullscreen>
            </div>
        </div>
    )
}

export default FolderMM
