import React, { useEffect, useState } from 'react'

import { TransformComponent } from 'react-zoom-pan-pinch'
import ContainerMM from './ContainerMM'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import * as actions from '../../store/actions'
import 'tui-image-editor/dist/tui-image-editor.css'

import BottomMenu from '../../components/Layout/BottomMenu'
import {
    Button,
    FormControlLabel,
    IconButton,
    Slider,
    Snackbar,
    Switch,
} from '@material-ui/core'
import { ArrowBack, Fullscreen } from '@material-ui/icons'

import Layout from '../../components/Layout/Layout'
import { withRouter, Prompt } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useBeforeunload } from 'react-beforeunload'
import { SketchPicker } from 'react-color'

const MindMap = props => {
    const { t } = useTranslation()
    const [selectedBox, setSelectedBox] = useState('')

    const [changeParentMode, setChangeParentMode] = useState('')
    const [editorMode, setEditorMode] = useState(false)
    const {
        saveStructure,
        account,
        profile,
        setEventIsFromDraggable,
        scale,
        structureNotSaved,
        setScale,
        resetTransform,
    } = props
    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id
    useBeforeunload(() => structureNotSaved && "You'll lose your data!")

    useEffect(() => {
        document.body.style.overflow = 'hidden'
        return () => {
            document.body.style.overflow = 'auto'
        }
    }, [])

    useEffect(() => {
        setEditorMode(false)
    }, [isOwner])

    // const [value, setValue] = useState(scale)

    // useEffect(() => {
    //     if (scale !== value) setScale(value)
    // }, [value])

    // useEffect(() => {
    //     if (scale !== value) setValue(scale)
    // }, [scale])

    const handleMouseDown = e => {
        if (editorMode) {
            const path = e.nativeEvent.composedPath()
            for (let el of path) {
                if (el) {
                    if (
                        el.getAttribute('id') === 'panWrap' ||
                        el === document.body
                    ) {
                        setEventIsFromDraggable(false)
                        return
                    }
                    const classN = el.getAttribute('class')
                    if (
                        classN &&
                        (classN.indexOf('react-draggable') > -1 ||
                            classN.indexOf('tinyEditorText_') > -1 ||
                            classN.indexOf('demo-toolbar-absolute') > -1 ||
                            classN.indexOf('noPan') > -1)
                    ) {
                        setEventIsFromDraggable(true)
                        return
                    }
                }
            }
        }
        setEventIsFromDraggable(false)
    }

    const handleClick = e => {
        // if (!changeParentMode) {
        //     if (editorMode) {
        //         const path = e.nativeEvent.composedPath()
        //         for (let el of path) {
        //             if (el) {
        //                 if (
        //                     el.getAttribute('id') === 'panWrap' ||
        //                     el === document.body
        //                 ) {
        //                     // setChosenResource('')
        //                     setSelectedBox('')
        //                     return
        //                 }
        //                 const classN = el.getAttribute('class')
        //                 if (
        //                     classN &&
        //                     (classN.indexOf('react-draggable') > -1 ||
        //                         classN.indexOf('tinyEditorText_') > -1)
        //                 ) {
        //                     return
        //                 }
        //             }
        //         }
        //     }
        //     // setChosenResource('')
        //     setSelectedBox('')
        // }
    }

    return (
        <>
            <Prompt
                when={structureNotSaved}
                message="You have unsaved changes, are you sure you want to leave?"
            />
            <div
                style={{
                    display: 'flex',
                    width: '100%',
                    height: '100%',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        background: 'white',
                        zIndex: 1,
                        display: 'flex',
                    }}
                >
                    {isOwner ? (
                        <Layout
                            noContent
                            customHeader={
                                <>
                                    <FormControlLabel
                                        control={
                                            <Switch
                                                checked={editorMode}
                                                onChange={event => {
                                                    setEditorMode(
                                                        event.target.checked
                                                    )
                                                    setSelectedBox('')
                                                }}
                                                color="primary"
                                            />
                                        }
                                        label={t('g.edit')}
                                        labelPlacement="start"
                                    />

                                    {isOwner &&
                                        editorMode &&
                                        structureNotSaved && (
                                            <Button
                                                onClick={saveStructure}
                                                color="primary"
                                                variant="contained"
                                                style={{
                                                    marginLeft: '20px',
                                                }}
                                            >
                                                {t('g.save')}
                                            </Button>
                                        )}
                                    <div
                                        style={{
                                            position: 'fixed',
                                            right: '10px',
                                            bottom: '10px',
                                            display: 'flex',
                                            flexDirection: 'column',
                                            background: 'white',
                                            border: '1px #ccc solid',
                                            padding: '5px 0px',
                                            alignItems: 'center',
                                        }}
                                    >
                                        <Slider
                                            min={0.1}
                                            max={3}
                                            step={0.1}
                                            value={scale}
                                            onChange={(e, value) =>
                                                setScale(value)
                                            }
                                            style={{
                                                height: '100px',
                                            }}
                                            orientation="vertical"
                                        />
                                        <IconButton
                                            onClick={resetTransform}
                                            color="primary"
                                        >
                                            <Fullscreen />
                                        </IconButton>
                                    </div>
                                </>
                            }
                        />
                    ) : (
                        <Layout noContent />
                    )}
                </div>

                <div
                    style={{
                        overflow: 'hidden',
                        flex: 1,
                        display: 'flex',
                    }}
                    id="panWrap"
                    onMouseDown={handleMouseDown}
                    onTouchStart={handleMouseDown}
                    onTouchEnd={() => setEventIsFromDraggable(false)}
                    onMouseUp={() => setEventIsFromDraggable(false)}
                    onClick={handleClick}
                >
                    <TransformComponent>
                        <div
                            style={{
                                cursor: 'grabbing',
                            }}
                        >
                            <ContainerMM
                                id="b0"
                                setSelectedBox={setSelectedBox}
                                selectedBox={selectedBox}
                                scale={scale}
                                editorMode={editorMode}
                                changeParentMode={changeParentMode}
                                setChangeParentMode={setChangeParentMode}
                            />
                        </div>
                    </TransformComponent>
                </div>
            </div>

            <BottomMenu />
            <Snackbar
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                open={Boolean(changeParentMode)}
                message={t('g.chooseParent')}
                action={
                    <React.Fragment>
                        <Button
                            color="secondary"
                            size="small"
                            onClick={() => setChangeParentMode('')}
                        >
                            {t('g.UNDO')}
                        </Button>
                    </React.Fragment>
                }
            />
        </>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        structureNotSaved: state.structureNotSaved,
        scale: state.scaleMM,
    }
}
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveStructure: () => dispatch(actions.saveStructure()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(MindMap)
)
